<template>
  <el-form :inline="true" :model="formInline" class="demo-form-inline" >
    <el-row>
    <el-form-item  style="margin-top: 20px;margin-left:-480px" label="用户名称" prop="username" >
       <el-select v-model="comp.username" style="width: 100%;"  @change="onSubmit">
        <el-option v-for="item in userList" :key="item.username":label="item.username" :value="item.username"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item style="margin-top: 20px;"  label="项目名称" prop="id">
      <el-select v-model="project.id" style="width: 100%;" @change="onSubmit">
        <el-option v-for="item in projectNameList" :key="item.id":label="item.name" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
      <el-form-item style="margin-top: 20px;">
        <el-button type="primary" icon="el-icon-search" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-row>
    <el-row>
      <template>
        <el-transfer style="margin-left:-450px;padding-left: 15px" v-model="value1":data="data":titles="dataTitles"></el-transfer>
      </template>
    </el-row>
    <el-row>
      <el-form-item style="margin-top: 20px;margin-left: -400px" >
        <el-button type="primary" size="mini" icon="el-icon-plus" @click="addAssociation" :disabled="boolBr.length>0"> 添加关系</el-button>
      </el-form-item>
      <el-form-item style="margin-top: 20px;" >
        <el-button type="primary" size="mini" icon="el-icon-edit" @click="modifyAssociation" :disabled="boolBr.length==0"> 修改关系</el-button>
      </el-form-item>
    </el-row>
  </el-form>
</template>

<script>
  export default {
    data() {
      return {
        formInline: {
          user: '',
          region: ''
        },
        dataTitles:["待分配角色","已选中"],

        boolBr:[],
        data: [],
        value1:[],
        userList: [],
        projectNameList:[],
        comp: {
          username: ''
        },
        project:{
          id:'',
          mame:''
        },
        rules:{
          username: [{required: true, message: '必填:用户名称', trigger: 'change'}],
          id: [{required: true, message: '必填:项目名称', trigger: 'change'}],
        }
      }
    },
    mounted: function () {
      this.loadEmps();
      this.generateData();
    },
    methods: {
      onSubmit(){

        if(this.comp.username == null || this.comp.username === "" || this.project.id == null || this.project.id === "")return;


        this.getRequest('/aut/association/role?username='+this.comp.username+'&projectId='+this.project.id).then(resp => {
          if(resp && resp.status === 200) {
            const DataResource = [];
            for (let j = 0; j<resp.data.msg.length; j++) {
              DataResource.push(resp.data.msg[j].id);
            }
             this.value1=DataResource;
             this.boolBr=DataResource;
            };
        });
      },
      loadEmps(){
        const _this = this;

        this.postRequestJson('/aut/user/list',{
          username:""
        }).then(resp=> {
          if (resp && resp.status === 200) {
            const data = resp.data;
            _this.userList =data.msg.result;
          }
        });
        this.postRequestJson('/aut/project/list',{
          projectName: this.projectName,
        }).then(resp=> {
          if (resp && resp.status === 200) {
            const data = resp.data;
            _this.projectNameList =data.msg.result;

          }
        })
      },
      generateData() {
        //查询全部角色
        this.postRequestJson('/aut/role/list',{
          roleName:'',
          isDisabled:false
        }).then(resp=> {
          if (resp && resp.status === 200) {
            const data = resp.data;
            const roleData = [];
            for (let i = 0; i <data.msg.result.length; i++) {
              roleData.push({
                key: data.msg.result[i].id,
                label: data.msg.result[i].roleName,
              });
            }
            this.data=roleData;
          }else{

          }
        });
      },
      addAssociation(){
        if(this.comp.username!="" && this.project.id!=""){
          this.postRequestJson("/aut/association",{
            username:this.comp.username,
            businessDbId:this.project.id,
            roleIds:this.value1
          }).then(resp=> {
            if (resp && resp.status == 200) {
              this.$confirm( resp.data.msg, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
              }).catch(() => {
              });
            }else{

            }
          })
        }else{
          this.$confirm( "请填好必填项", '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
          }).catch(() => {
          });
        }

      },
      modifyAssociation(){
        this.putRequestJson("/aut/association",{
          username:this.comp.username,
          businessDbId:this.project.id,
          roleIds:this.value1
        }).then(resp=> {
          if (resp && resp.status === 200) {
            this.$confirm( resp.data.msg, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
            }).catch(() => {
            });
          }else{
            this.$confirm( resp.status, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
            }).catch(() => {
            });
          }
        })
      }

    }}
</script>
<style>
  .el-transfer-panel .el-checkbox__inner {
    height: 14px;
    width: 14px;
    border-radius: 3px;
    margin-left: -45px;
  }
  .el-transfer-panel .el-transfer-panel__header .el-checkbox .el-checkbox__label {
    font-size: 16px;
    color: #303133;
    margin-left: -19px;
    font-weight: 400;
  }
</style>



