<template>

  <div style="margin-top: 20px">

    <el-form>
      <el-row>
        <el-form-item>
          <el-row>
            <el-col :span="10">
              <el-form-item label="日期范围:">
                <el-date-picker
                  v-model="date"
                  value-format="yyyy-MM-dd"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>

            </el-col>
            <el-col :span="4">
              <el-button type="primary" @click="loadLogList">查询</el-button>
            </el-col>

          </el-row>


        </el-form-item>
      </el-row>
      <el-row>
        <el-table
          :data="logList"
          v-loading="tableLoading"
          border
          size="mini"
          style="width: 100%">
          <el-table-column
            prop="id"
            label="id"
            align="center">
          </el-table-column>
          <el-table-column
            prop="operationName"
            align="center"
            label="操作名称">
          </el-table-column>
          <el-table-column
            prop="operator"
            align="center"
            label="操作人">
          </el-table-column>
          <el-table-column
            prop="dateTime"
            align="center"
            label="操作时间">
          </el-table-column>
          <el-table-column
            prop="interface"
            align="center"
            label="请求接口">
          </el-table-column>
          <el-table-column
            prop="timeConsuming"
            align="center"
            label="耗时">
          </el-table-column>
          <el-table-column
            prop="ipAddress"
            align="center"
            label="ip地址">
          </el-table-column>
        </el-table>
      </el-row>
      <el-pagination
        background
        :page-size="10"
        :current-page="searchCondition.curPage"

        @current-change="currentChange"
        layout="prev, pager, next"
        :total="searchCondition.totalSize">
      </el-pagination>
    </el-form>


  </div>


</template>

<script>
  export default {
    data() {
      return {
        logList: [],
        tableLoading: false,
        searchCondition:{
          curPage:1,
          totalSize:0,
          pageSize:10,
          startDate:null,
          endDate:null
        },
        date:''

      };
    },
    mounted: function () {

      this.loadLogList();

    },
    methods: {

      currentChange(data){
        console.log(data);
        this.searchCondition.curPage = data;
        this.loadLogList();

      },


      loadLogList() {
        this.tableLoading = true;
        this.logList = [];
        if(this.date != null || this.date != ''){
          this.searchCondition.startDate = this.date[0];
          this.searchCondition.endDate = this.date[1];
        }else{
          this.searchCondition.startDate = null;
          this.searchCondition.endDate = null;
        }
        this.postRequestJson('/aut/log',{
          curPage:this.searchCondition.curPage,
          pageSize:this.searchCondition.pageSize,
          startDate:this.searchCondition.startDate,
          endDate:this.searchCondition.endDate
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200 && resp.data.code == '200') {
            let result = resp.data.msg;
            this.searchCondition.curPage = result.curPage;
            this.searchCondition.pageSize = result.pageSize;
            this.searchCondition.totalSize = result.totalSize;
            let list = result.result;
            for (let i = 0; i < list.length; i++) {
              this.logList.push({
                id: list[i].id,
                operationName: list[i].operationName,
                operator: list[i].operator,
                dateTime: list[i].dateTime,
                interface: list[i].interfaces,
                timeConsuming: list[i].timeConsuming,
                ipAddress: list[i].ipAddress
              });
            }
          }

        });
      },


      showEditEmpView(row) {
        this.resourceVisible = true;
        this.curResource.id = row.id;
        this.curResource.faId = row.faId;
        this.curResource.name = row.name;
        this.curResource.uri = row.uri;
        this.curResource.type = row.type;
        this.curResource.icon = row.icon;
        this.curResource.version = row.version;
        this.curResource.requestMethod = row.requestMethod;
        this.curResource.scope = row.scope;
        this.curResource.componet = row.componet;


      },
      madyifResource() {
        this.dialogTitle = "编辑资源信息";
        this.resourceVisible = false;
        this.putRequestJson("/aut/resource", {
          id: this.curResource.id,
          componet: this.curResource.componet
        }).then(resp => {
          if (resp && resp.status == 200 && resp.data.code == '200') {
            this.$message({
              type: 'success',
              message: resp.data.msg
            });
          } else {
            this.$message.error({
              type: 'fail',
              message: resp.data.msg
            });
          }
        })
      },
    }
  }
</script>

<style scoped>

</style>



