<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder="通过员工名搜索员工,记得回车哦..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="username">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            添加用户
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="userList"
            v-loading="tableLoading"
            border
            style="width: 100%">
            <el-table-column
              prop="username"
              align="center"
              label="用户名">
            </el-table-column>
            <el-table-column
              prop="nickname"
              align="center"
              label="别名">
            </el-table-column>
            <el-table-column
              prop="password"
              label="密码"
              align="center">
            </el-table-column>
            <el-table-column
              prop="isDisabled"
              align="center"
              label="使用状态">
            </el-table-column>
            <el-table-column
              align="center"
              label="操作">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">编辑
                </el-button>
                <el-button  @click="deleteEmp(scope.row)"   type="danger" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>
    <el-form :model="emp" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="65%">
          <el-row>
            <el-col :span="7">
            <div>
              <el-form-item label="用户名:" prop="username">
                <el-input prefix-icon="el-icon-edit" v-model="emp.username"  size="mini" style="width: 150px"
                          placeholder="请输入用户名"></el-input>
              </el-form-item>
            </div>
          </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="别名:" prop="nickname">
                  <el-input  prefix-icon="el-icon-edit" v-model="emp.nickname"   size="mini" style="width: 120px" placeholder="别名"></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="密码:" prop="password">
                  <el-input prefix-icon=" el-icon-edit"  v-model="emp.password"  size="mini" style="width: 120px" placeholder="密码"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>

          </el-row>
              <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


    <el-form :model="modifyUser" :rules="rules" ref="modifyEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible2"  width="65%">
          <el-row>
            <el-col :span="7">
              <div>
                <el-form-item label="用户名:" prop="username">
                  <el-input prefix-icon="el-icon-edit" v-model="modifyUser.username":disabled="true" size="mini" style="width: 150px"
                            placeholder="请输入用户名"></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="别名:" prop="nickname">
                  <el-input  prefix-icon="el-icon-edit"  v-model="modifyUser.nickname" size="mini" style="width: 120px" placeholder="别名"></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="密码:" prop="password">
                  <el-input prefix-icon=" el-icon-edit"  v-model="modifyUser.password" size="mini" style="width: 120px" placeholder="密码"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>

          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="modifyUser1('modifyEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


  </div>
</template>
<script>
  export default {
    data() {
      return {
        userList: [],
        roleList:[],
        username: '',
        beginDateScope: '',
        dialogTitle: '',
        multipleSelection: [],
        nations: [],
        politics: [],
        positions: [],
        totalCount: -1,
        currentPage: 1,
        dialogVisible: false,
        dialogVisible2:false,
        tableLoading: false,
        advanceSearchViewVisible: false,
        emp: {
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          password:'',
          projectName: ''
        },
        modifyUser: {
          username: '',
          nickname: '',
          password:'',
        },
        rules: {
          username: [{required: true, message: '必填:用户名', trigger: 'blur'}],
          nickname: [{required: true, message: '必填:别名', trigger: 'blur'}],
          password: [{required: true, message: '必填:密码', trigger: 'blur'}],
          phone: [{required: true, message: '必填:电话号码', trigger: 'blur'}],
          address: [{required: true, message: '必填:联系地址', trigger: 'blur'}],

        }
      };
    },
    mounted: function () {
     // this.initData();
      this.loadEmps();
    },
    methods: {
      handleSelectionChange(val) {
       // this.multipleSelection = val;
      },
      deleteManyEmps(){
        this.$confirm('此操作将删除[' + this.multipleSelection.length + ']条数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].username + ",";
          }
          this.doDelete(ids);
        }).catch(() => {
        });
      },
      deleteEmp(row){
        this.$confirm('此操作将永久删除[' + row.username + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.username);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
        });
      },
      doDelete(ids){
        this.tableLoading = true;
        var _this = this;
       this.deleteRequest("/aut/user?username="+ ids+"&disable=true").then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            var data = resp.data;
            _this.loadEmps();
          }
        });
      },
      keywordsChange(val){
        if (val == '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      loadEmps(){
        const _this = this;
        this.tableLoading = true;

        this.postRequestJson('/aut/user/list',{
            username: this.username,
            curPage: this.currentPage,
            nickname:this.emp.nickname
          }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
             if(resp.data.code==="200"){
               const data = resp.data;
               const userData=[];
               var nameDis="使用中";
               for (let i=0;i<data.msg.result.length;i++) {
                 if(data.msg.result[i].isDisabled){
                   nameDis="已弃用";
                 }else{
                   nameDis="使用中";

                 }
                 userData.push({
                   username:data.msg.result[i].username,
                   nickname:data.msg.result[i].nickname,
                   isDisabled:nameDis,
                 })
               }
               _this.userList =userData;
               _this.totalCount = data.msg.totalSize;
               _this.emptyEmpData();
             }else{
               this.getMsg(resp.data.msg);
             }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.emp.id) {
              //更新
              this.tableLoading = true;
              this.putRequestJson("/aut/user", this.modifyUser).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  if(resp.data.code==="200"){
                    _this.dialogVisible2 = false;
                    _this.emptyEmpData();
                    _this.loadEmps();
                  }else{
                    this.getMsg(resp.data.msg);
                  }
                }else{
                  this.getMsg(resp.status);
                }
              })
            } else {
              //添加
             this.tableLoading = true;
              this.postRequestJson("/aut/user", this.emp).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  if(resp.data.code==="200"){
                    _this.dialogVisible = false;
                    _this.emptyEmpData();
                    _this.loadEmps();
                  }else{
                    this.getMsg(resp.data.msg);
                  }
                }else{
                  this.getMsg(resp.status);
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      modifyUser1(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //更新
            this.tableLoading = true;
            this.putRequestJson("/aut/user", this.modifyUser).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status === 200) {
                if(resp.data.code==="200"){
                  _this.dialogVisible2 = false;
                  _this.emptyEmpData();
                  _this.loadEmps();
                }else{
                  this.getMsg(resp.data.msg);
                }
              }else{
                this.getMsg(resp.status);
              }
            })
          } else {
            return false;
          }
        });
      },
      cancelEidt(){
        this.dialogVisible = false;
      },
      showEditEmpView(row){
        this.dialogTitle = "编辑员工";
        this.modifyUser.username = row.username;
        this.modifyUser.nickname = row.nickname;
        this.dialogVisible2 = true;
      },
      showAddEmpView(){
        this.dialogTitle = "添加用户";
        this.dialogVisible = true;
      },
      getMsg(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        }).catch(() => {
        });
      },
      emptyEmpData(){
        this.emp = {
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          projectName: ''
        }
      }
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



