<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder="通过角色名搜索角色信息,记得回车哦..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="roleName">
          </el-input>
      <!--    <el-checkbox v-model="checked">是否可用</el-checkbox>-->
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            添加角色
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="roleList"
            v-loading="tableLoading"
            border

            size="mini"
            style="width: 100%">
            <el-table-column
              prop="id"
              align="center"
              label="序号"
             >
            </el-table-column>
            <el-table-column
              prop="roleName"
              align="center"
              label="角色名称">
            </el-table-column>
            <el-table-column
              prop="isDisabled"
              align="center"
              label="使用状态">
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              width="200">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">编辑
                </el-button>
                <el-button  @click="deleteEmp(scope.row)"   type="danger" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>

      </el-main>
    </el-container>


    <el-form :model="emp" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <div>
                <el-form-item label="角色名称:" prop="roleName">
                  <el-input prefix-icon="el-icon-edit" v-model="emp.roleName"  size="mini" style="width: 150px"
                            placeholder="请输入角色名称"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-table
              :data="resourceList"
              v-loading="tableLoading"
              border
              stripe
              @selection-change="SelectionChange"
              size="mini"
              style="width: 100%">
              <el-table-column
                type="selection"
                align="left"
                width="30">
              </el-table-column>
              <el-table-column
                prop="id"
                align="center"
                fixed
                label="记录号"
              >
              </el-table-column>
              <el-table-column
                prop="name"
                fixed
                align="center"
                label="资源名">
              </el-table-column>
            </el-table>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


    <el-form :model="modifyResource" :rules="rules" ref="modifyEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible2"  width="65%">
          <el-row>
            <el-col :span="8">
              <div>
                <el-form-item label="角色名称:" prop="name">
                  <el-input prefix-icon="el-icon-edit" v-model="modifyResource.name":disabled="true" size="mini" style="width: 150px"
                            placeholder="请输入角色名称"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <template>
              <el-transfer v-model="value1" :data="data" :titles="dataTitles"></el-transfer>
            </template>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="modifyResource1('modifyEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        resourceList: [],
        roleList:[],
        beginDateScope: '',
        dataTitles:["待分配资源","已选中"],
        roleName:'',
        checked:false,
        dialogTitle: '',
        multipleSelection: [],
        resourceSelection:[],
        totalCount: -1,
        currentPage: 1,
        dialogVisible: false,
        dialogVisible2:false,
        tableLoading: false,
        advanceSearchViewVisible: false,
        emp: {
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          password:'',
          projectName: ''
        },
        modifyResource: {
          name: '',
          id:''
        },
        rules: {
          roleName: [{required: true, message: '必填:角色名称', trigger: 'blur'}],
          nickname: [{required: true, message: '必填:别名', trigger: 'blur'}],
          password: [{required: true, message: '必填:密码', trigger: 'blur'}],
          phone: [{required: true, message: '必填:电话号码', trigger: 'blur'}],
          address: [{required: true, message: '必填:联系地址', trigger: 'blur'}],

        },
        data: [],
        value1:[]

      };

    },
    mounted: function () {
      this.loadEmps();
    },
    methods: {
      handleSelectionChange(val) {
       // this.multipleSelection = val;
      },
      SelectionChange(val){
        this.resourceSelection = val;
      },
      deleteEmp(row){
        this.$confirm('此操作将永久删除[' + row.roleName + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
        });
      },
      doDelete(ids){
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/aut/role?id="+ids+"&disable=true").then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              _this.loadEmps();
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        });
      },
      keywordsChange(val){
        if (val === '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      loadEmps(){
        const _this = this;
        this.tableLoading = true;
        this.postRequestJson('/aut/role/list',{
          roleName: this.roleName,
          curPage: this.currentPage,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              const data = resp.data;
              const roleData=[];
              var nameDis="使用中";
              for (let i=0;i<data.msg.result.length;i++) {
                if(data.msg.result[i].isDisabled){
                  nameDis="已弃用";
                }else{
                  nameDis="使用中";
                }
                roleData.push({
                  id:data.msg.result[i].id,
                  roleName:data.msg.result[i].roleName,
                  isDisabled:nameDis,
                })
              }
              _this.roleList =roleData;
              _this.totalCount = data.msg.totalSize;
              _this.emptyEmpData();
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //添加角色
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = new Array();
            for(let i=0;i<this.resourceSelection.length;i++){
              data.push(this.resourceSelection[i].id);
            }
            //添加
           this.tableLoading = true;
            this.postRequestJson("/aut/role",{
              roleName:this.emp.roleName,
              auths:data
            }).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status === 200) {
                 if(resp.data.code==="200"){
                   _this.dialogVisible = false;
                   _this.loadEmps();
                 }else{
                   this.getMsg(resp.data.msg);
                 }
              }else{
                this.getMsg(resp.status);
              }
            })
          } else {
            return false;
          }
        });
      },
      //修改
      modifyResource1(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //更新
            this.tableLoading = true;
             this.putRequestJson("/aut/role",{
             id:this.modifyResource.id,
             auths:this.value1
           }).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status === 200) {
                if(resp.data.code==="200"){
                  _this.dialogVisible2 = false;
                  _this.loadEmps();
                }else{
                  this.getMsg(resp.data.msg);
                }
              }else{
                this.getMsg(resp.status);
              }
            })
          } else {
            return false;
          }
        });
      },
      cancelEidt(){
        this.dialogVisible = false;
        this.dialogVisible2= false;
        this.emptyEmpData();
      },
      showEditEmpView(row){
        console.log(row)
        this.dialogTitle = "编辑角色";
        this.modifyResource.name = row.roleName;
        this.modifyResource.id = row.id;
        this.dialogVisible2 = true;
        this.generateData(row.id);
      },

      generateData (id) {
        //查询全部资源
        this.getRequest('/aut/resource').then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {

            if(resp.data.code==="200"){
              const data = resp.data;
              const resourceData = [];
              for (let i = 0; i <data.msg.length; i++) {
                resourceData.push({
                  key: data.msg[i].id,
                  label: data.msg[i].name,
                });
              }
              this.data=resourceData;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        });
        //查询拥有的资源
        this.getRequest('/aut/role?id='+id).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
             if(resp.data.code==="200"){
               const data = resp.data;
               const DataResource = [];
               for (let j = 0; j<data.msg.auths.length; j++) {
                 DataResource.push(data.msg.auths[j].id);
               }
               this.value1=DataResource;
             }else{
               this.getMsg(resp.data.msg);
             }

          }else{
            this.getMsg(resp.status);
          }
        })
      },

        showAddEmpView(){
        this.dialogTitle = "添加角色";
        this.dialogVisible = true;
        const _this = this;
        this.getRequest('/aut/resource').then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
              if(resp.data.code==="200"){
                const data = resp.data;
                _this.resourceList =data.msg;
              }else{
                this.getMsg(resp.data.msg);
              }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      getMsg(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        }).catch(() => {
        });
      },
      emptyEmpData(){
        this.emp = {
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          projectName: ''
        }
      }
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



