<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder="通过项目名名搜索角色信息,记得回车哦..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="projectName">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            添加项目
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="projectNameList"
            v-loading="tableLoading"
            border
            size="mini"
            style="width: 100%">
            <el-table-column
              prop="id"
              align="center"
              label="项目记录号"
              width="120">
            </el-table-column>
            <el-table-column
              prop="code"
              align="center"
              label="项目编号"
              width="200"
            >
            </el-table-column>
            <el-table-column
              prop="name"
              align="center"
              width="200"
              label="项目名称"
            >
            </el-table-column>
            <el-table-column
              prop="creator"
              label="创建人"
              align="center"
              width="110"
            >
            </el-table-column>
            <el-table-column
              prop="createTime"
              label="创建时间"
              align="center"
              width="200"
            >
            </el-table-column>
            <el-table-column
              prop="isDisabled"
              align="center"
              label="使用状态"
              width="200"
            >
            </el-table-column>
            <el-table-column
              label="操作"
              align="center"
              fixed="right"
              >
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">编辑
                </el-button>
                <el-button  @click="deleteEmp(scope.row)"   type="danger" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>
    <el-form :model="projectDaName" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="25%">
          <el-row>
            <el-col :span="9">
              <div>
                <el-form-item label="项目名称:" prop="name">
                  <el-input prefix-icon="el-icon-edit" v-model="projectDaName.name"  size="mini" style="width: 150px"
                            placeholder="请输入项目名称"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>

  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        projectNameList: [],
        roleList:[],
        projectName: '',
        dialogTitle: '',
        multipleSelection: [],
        totalCount: -1,
        currentPage: 1,
        dialogVisible: false,
        tableLoading: false,
        advanceSearchViewVisible: false,
        emp: {
          id:'',
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          password:'',
          projectName: ''
        },

        projectDaName:{
          id:'',
          name:'',
        },

        rules: {
          name: [{required: true, message: '必填:项目名称', trigger: 'blur'}]
        }
      };
    },
    mounted: function () {
      this.loadEmps();
    },
    methods: {

      handleSelectionChange(val) {
        //this.multipleSelection = val;
      },
      deleteEmp(row){
        this.$confirm('此操作将永久删除[' + row.name + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
        });
      },
      doDelete(ids){
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/aut/project?projectId="+ ids+"&disable=true").then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            if( resp.data.code==="200"){
              this.getMsg(resp.data.msg);
              _this.loadEmps();
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        });
      },
      keywordsChange(val){
        if (val === '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      loadEmps(){
        const _this = this;
        this.tableLoading = true;
        this.postRequestJson('/aut/project/list',{
          projectName: this.projectName,
          curPage: this.currentPage
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {

            if( resp.data.code==="200"){
              const data = resp.data;
              const projectData=[];
              var nameDis="使用中";
              for (let i=0;i<data.msg.result.length;i++) {
                if(data.msg.result[i].isDisabled){
                  nameDis="已弃用";
                }else{
                  nameDis="使用中";

                }
                projectData.push({
                  name:data.msg.result[i].name,
                  code:data.msg.result[i].code,
                  id:data.msg.result[i].id,
                  createTime:data.msg.result[i].createTime,
                  creator:data.msg.result[i].creator,
                  isDisabled:nameDis,
                })
              }
              _this.projectNameList =projectData;
              _this.totalCount = data.msg.totalSize;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //添加与修改
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log("gfnjgn");
            console.log(this.emp.id);
            if (this.projectDaName.id) {
              //更新
              this.tableLoading = true;
              this.putRequestJson("/aut/project", this.projectDaName).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                   if(resp.data.code==="200"){
                     _this.dialogVisible = false;
                     _this.emptyEmpData();
                     _this.loadEmps();
                   }else{
                     this.getMsg(resp.data.msg);
                   }
                }else{
                  this.getMsg(resp.status);
                }
              })
            } else {
              //添加
              this.tableLoading = true;
              this.postRequestJson("/aut/project", {
                 name:this.projectDaName.name ,
              }).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  if(resp.data.code==="200"){
                    _this.dialogVisible = false;
                    _this.emptyEmpData();
                    _this.loadEmps();
                  }else{
                    this.getMsg(resp.data.msg);
                  }
                }else{
                  this.getMsg(resp.status);
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      cancelEidt(){
        this.dialogVisible = false;
        this.emptyEmpData();
      },
      showEditEmpView(row){
        this.dialogTitle = "编辑项目";
        this.projectDaName.name = row.name;
        this.projectDaName.id = row.id;
        this.dialogVisible = true;
      },
      showAddEmpView(){
        this.dialogTitle = "添加项目";
        this.dialogVisible = true;
      },
      getMsg(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        }).catch(() => {
        });
      },
      emptyEmpData(){
        this.emp = {
          username: '',
          nickname: '',
          isLocked: '',
          isDisabled: '',
          roleName: '',
          projectName: ''
        }
      }
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



